body {
    font: 34px "Century Gothic", Futura, sans-serif;
    margin: 40px;
    
  }
  
  ol, ul {
    padding-left: 50px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
    padding-left: 35px;
  }
  
  .square {
    background: #C9F76F;
    border: 1px solid #999;
    float: left;
    font-size: 84px;
    font-weight: bold;
    line-height: 104px;
    height: 104px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 104px;
  }
  
  .square:focus {
    outline: none;
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
  }
  
  .game-info {
    margin-left: 40px;
    color: #35127B;
  }
  


